import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import MenuItem from "./MenuItem"
import { Link } from "gatsby"
import Logo from "./Logo"

const Menu = () => {
  const data = useStaticQuery(graphql`
    query GETMAINMENU {
      allWpMenuItem(filter: { locations: { eq: TOP } }) {
        nodes {
          id
          label
          url
          title
          target
          cssClasses
          childItems {
            nodes {
              id
              label
              url
              title
              target
              cssClasses
              childItems {
                nodes {
                  id
                  label
                  url
                  title
                  target
                  cssClasses
                }
              }
            }
          }
        }
      }
      wp {
        generalSettings {
          url
        }
      }
    }
  `)
  const menuItems = data.allWpMenuItem.nodes
  const wordPressUrl = data.wp.generalSettings.url
  return (
    <header>
      <nav className="navbar navbar-expand-lg fixed-top navbar-light bg-light">
        <div className="container">
          <Link to="/" className="navbar-brand">
            <Logo />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto">
              {menuItems &&
                menuItems.map((prop, index) => (
                  <li className={prop.cssClasses + " nav-item"} key={index}>
                    <MenuItem
                      key={prop.id}
                      menuItem={prop}
                      wordPressUrl={wordPressUrl}
                    />
                    <div className="dropdown-menu">
                      {prop &&
                        prop.childItems &&
                        prop.childItems.nodes &&
                        prop.childItems.nodes.map((child, index) => {
                          return (
                            <div className="nav-item-sub" key={index}>
                              <MenuItem
                                key={child.id}
                                menuItem={child}
                                wordPressUrl={wordPressUrl}
                              />
                            </div>
                          )
                        })}
                    </div>
                  </li>
                ))}
            </ul>
          </div>
        </div>
      </nav>
    </header>
  )
}

export default Menu
